html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
}

p {
  font-size: 2rem;
  line-height: 1.5em;
}

a {
  text-decoration: none;
  color: #000000;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  margin: 0 0;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: auto;
}

.contenedor {
  width: 100%;
  margin: 0 0;
}

.contenedor__grid {
  display: grid;
  grid-template-columns: 1fr;
}

@media (min-width: 768px) {
  .contenedor__grid {
    grid-template-columns: 5fr 1fr;
    min-height: 100rem;
  }
}
.header {
  text-align: center;
  display: block;
  background-color: #1c1916;
  padding: 3rem;
  border-bottom: 1px solid #ffd53a;
}
.header__contenedor {
  display: flex;
  justify-content: space-around;
  gap: 5rem;
}
.header__heading {
  color: #ffd53a;
  font-size: 5rem;
  letter-spacing: 2rem;
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.header__contenedor-icono {
  padding: 0.5rem;
  border-radius: 50%;
  transition: all 8s ease;
}
.header__contenedor-icono:hover {
  background-color: #fbb106;
  border-radius: 50%;
  transform: rotate(3600deg);
}
.header__icono {
  border-radius: 50%;
  font-size: 10rem;
  color: #000000;
  background-color: #ffd53a;
  border: 1.5px solid #ffd53a;
  box-shadow: 5px 5px 3px inset #fbb106, 0px 0px 6px 3px #ffd53a;
}

.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  background-color: #282d35;
  color: #ffffff;
  border-left: 1px solid #384153;
  padding: 1.5rem 1rem 0 1rem;
}

.sidebar__entrada {
  font-size: 1.4rem;
}
.sidebar__entrada a {
  display: block;
  padding: 2rem;
  width: 20rem;
  color: #fbb106;
  text-align: center;
  border-radius: 1rem;
  background-color: #1c1916;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.sidebar__entrada a:hover {
  background-color: #221e1a;
}

.footer__contenedor {
  background-color: #1c1916;
  color: #ffffff;
  font-weight: 900;
}
.footer__contenedor p {
  text-align: center;
  margin: 0 auto;
  padding: 1rem;
  font-size: 2rem;
}

.wordFlip {
  margin: 3rem 3rem 0 3rem;
}
.wordFlip h2 {
  font-size: 5rem;
  padding: 3rem 0 3rem 0;
  text-align: center;
}
.wordFlip .formularioWF fieldset {
  display: block;
  border: none;
}
.wordFlip .formularioWF legend, .wordFlip .formularioWF label {
  font-size: 2rem;
  line-height: 3rem;
  margin-bottom: 2rem;
}
.wordFlip .formularioWF input {
  display: block;
  text-align: center;
  font-size: 1.5rem;
  width: 95%;
  height: 5rem;
  margin: 0 auto;
}
.wordFlip #resultadoWF {
  border-top: 1px solid #7D7470;
  padding-top: 1rem;
  text-align: center;
  color: #00b289;
}

.boton {
  text-align: center;
  background-color: #000000;
  color: #ffffff;
}
.boton p {
  padding: 1rem;
  font-size: 3rem;
}
.boton p:hover {
  color: #0ABF2E;
}

.inicio__contenedor {
  background-color: #384153;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10rem 2rem 0 2rem;
}
.inicio__contenedor-heading {
  color: #ffd53a;
}
.inicio__contenedor-heading h1 {
  font-size: 12rem;
  text-align: center;
}
.inicio__contenedor-fechas {
  display: flex;
  justify-content: space-between;
}
.inicio__nacimiento {
  text-align: center;
}
.inicio__nacimiento span {
  color: #fbb106;
}

.resultado_cuadricula {
  width: 100%;
  text-align: center;
  font-weight: 700;
}
.resultado_cuadricula p {
  font-size: 60px !important;
  margin: 0;
}