@use 'base/variables' as v;
@use 'base/mixins' as m;




.header {
    text-align: center;
    display: block;
    background-color: v.$DMgrisOscuro;
    padding: 3rem;
    border-bottom: 1px solid v.$amarillo;


    &__contenedor {

        display: flex;
        justify-content: space-around;
        gap: 5rem;
    }
    &__heading {
        color: v.$amarillo;
        font-size: 5rem;
        letter-spacing: 2rem;
        margin: 0.5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;

    }

    &__contenedor-icono {


        padding: .5rem;
        border-radius: 50%;
        transition: all 8s ease;

        &:hover {
            background-color: v.$dorado;
            border-radius: 50%;

            transform: rotate(3600deg);
        }
    }

    &__icono {

        border-radius: 50%;
        font-size: 10rem;
        color: v.$negro;

        background-color: v.$amarillo;
        border: 1.5px solid v.$amarillo;
        box-shadow: 5px 5px 3px inset v.$dorado,
        0px 0px 6px 3px  v.$amarillo;

    }








}





